@import '../../index.css';

.titlebar {
	width: 100%;
	display: flex;
	flex-grow: 1;
	margin-top: 50px;
	align-items: center;
}

.titlebar-left {
	display: flex;
	width: 33.33%;
	font-weight: 400px;
	font-size: 20px;
}

.nav-item {
	margin-left: 15px;
	cursor: pointer;
}

.nav-item:hover {
	text-decoration: underline;
}

.titlebar-center {
	width: 33.33%;
	height: 100%;
}

.titlebar-right {
	width: 33.33%;
}
.title {
	height: 100%;
	width: 100%;
	font-size: 36px;
	margin-bottom: 0;
}

.byline {
	font-size: 16px;
	font-weight: 300px;
	margin-top: 0;
}

@media only screen and (max-width: 700px) {
	.titlebar-left, .titlebar-right {
		display: none;
	}

	.titlebar-center {
		width: 100%;
	}
}
